footer {
  background: var(--color-bg-variant);
  color: var(--color-bg);
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
  padding-bottom: 7rem;
}

footer a {
  text-decoration: none;
}
.footer_logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 1rem;
  display: inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 2rem;
}

.footer_socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.footer_socials a {
  background: var(--color-bg);
  color: var(--color-white);
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
}

.footer_socials a:hover {
  background: transparent;
  color: var(--color-bg);
}

.footer_copyright {
  color: var(--color-bg);
}
